import React from "react"
import { Pagination } from "swiper"
import { Swiper, SwiperSlide } from "swiper/react"
import "./project.css"
import "swiper/swiper.min.css"
import "swiper/css/pagination"
import project from "../../assets/project.jpg"
import time from "../../assets/time-icon.svg"
import cost from "../../assets/cost-icon.svg"
import feedback from "../../assets/feedback-icon.svg"
import pizza from "../../assets/pizza.png"
import apex from "../../assets/apex.png"
function Project() {
  const [isActive, setIsActive] = React.useState(0)
  return (
    <>
      <Swiper
        className="project-slider"
        modules={[Pagination]}
        spaceBetween={50}
        slidesPerView={1}
        navigation
        pagination={{ clickable: true }}
        scrollbar={{ draggable: true }}
        onSlideChange={() => console.log("slide change")}
        onSwiper={(swiper) => console.log(swiper)}
      >
        <SwiperSlide>
          <div className="container">
            <h2 className="description title"> </h2>
            <div className="project">
              <div className="project__img">
                <img src={pizza}></img>
              </div>
              <div>
                <h5 className="project__title">Pizza -   </h5>
                <ul className="list">
                  <li className="list__item">
                    <div className="list__img">
                      <img src={time}></img>
                    </div>
                    <div>
                      <p className="project__desc"></p>
                      <span className="project__text">10 </span>
                    </div>
                  </li>
                  <li className="list__item">
                    <div className="list__img">
                      <img src={cost}></img>
                    </div>
                    <div>
                      <p className="project__desc"></p>
                      <span className="project__text"></span>
                    </div>
                  </li>
                </ul>
                <div className="content">
                  <div className="content__img">
                    <img src={feedback}></img>
                  </div>
                  <div className="content__desc">
                    <p className="project__desc"> </p>
                    <span className="project__text">
                      ,   React   Redux
                      Toolkit  SCSS,    , 
                      ,    .  
                      Redux Toolkit,   
                      ,      
                      .
                    </span>
                  </div>
                </div>
                <div className="project__links">
                  <a
                    href="https://pizza-56d88.web.app/"
                    target="_blank"
                    className="project__link"
                  >
                    <button className="project__btn btn" type="sumbit">
                        
                    </button>
                  </a>
                  <a
                    href="https://github.com/zlatov-anatoli/pizza"
                    target="_blank"
                    className="project__link"
                  >
                    <button className="project__btn btn" type="sumbit">
                        
                    </button>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="container">
            <h2 className="description title"> </h2>
            <div className="project">
              <div className="project__img">
                <img src={apex}></img>
              </div>
              <div className="project__body">
                <h5 className="project__title">
                  Apex -    
                </h5>
                <ul className="list">
                  <li className="list__item">
                    <div className="list__img">
                      <img src={time}></img>
                    </div>
                    <div>
                      <p className="project__desc"></p>
                      <span className="project__text">3 </span>
                    </div>
                  </li>
                  <li className="list__item">
                    <div className="list__img">
                      <img src={cost}></img>
                    </div>
                    <div>
                      <p className="project__desc"></p>
                      <span className="project__text"> </span>
                    </div>
                  </li>
                </ul>
                <div className="content">
                  <div className="content__img">
                    <img src={feedback}></img>
                  </div>
                  <div className="content__desc">
                    <p className="project__desc"> </p>
                    <span className="project__text">
                      ,   React,   Swiper
                           . Swiper
                           .
                       ,    SCSS 
                          
                    </span>
                  </div>
                </div>
                <div className="project__links">
                  <a
                    href="https://apex-3a1d1.web.app/"
                    target="_blank"
                    className="project__social"
                  >
                    <button className="project__btn btn" type="sumbit">
                        
                    </button>
                  </a>
                  <a
                    href="https://github.com/zlatov-anatoli/apex"
                    target="_blank"
                    className="project__social"
                  >
                    <button className="project__btn btn" type="sumbit">
                        
                    </button>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </SwiperSlide>
      </Swiper>
    </>
  )
}

export default Project
